import Vue from 'vue'
import VueResource from 'vue-resource'
import VueRouter from "vue-router"
import App from './App.vue'
import Router from "./router.js"


Vue.config.productionTip = false

Vue.use(VueResource)
Vue.use(VueRouter)


// 自定义指令
Vue.directive("raindow",{
  bind(el,binding,vnode){
    el.style.color = '#' + Math.random().toString(16).slice(2,8);
  }
})
 
Vue.directive('threm',{//当自定义的指令中含有参数时候，所写的参数中必须是字符串或者数组或者是一个对象
  bind(el,binding,vnode){
    if(binding.value == 'wide'){
      el.style.width = "1260px";
    }else if(binding.value == "normal"){
      el.style.width = "200px";
    }
  }
})


// 自定义过滤器（全局）

/*
Vue.filter("to-uppercase",function(value){
  return value.toUpperCase();
})*/

Vue.filter("to-uppercase",function(value){
  return value.charAt(0).toUpperCase() + value.slice(1).toLowerCase();
})
 /*charAt()是指定位置的字符，toLowerCase()是将所有的英文字母变成小写 */

//只显示100个字符
Vue.filter("to-hidden",function(value){
  return value.slice(0,100) + "..."
})

// 创建路由
const route =  new VueRouter({
  routes:Router,
  mode:"history"//不会出现#号
})

new Vue({
  render: h => h(App),
  router:route
}).$mount('#app')
