<template>
    <div class="singleBlog">
        <h1>
            {{blog.title}}
        </h1>
        <article>
            {{blog.content}}
        </article>
        <p>作者：{{blog.author}}</p>
        <p>分类：
            <ul>
                <li v-for="classify in blog.classifies" :key="classify">
                    {{classify}}
                </li>
            </ul>
        </p>
        <div class="btn123">
            <button @click="Delete()">删除</button>
            <router-link :to="'/edit/'+id">修改博客</router-link>
        </div>
        
    </div>
</template>
<script>
    export default{
        name:'single-blog',
        data(){
            return{
                id:this.$route.params.id,
                blog:{}
            }
        },
        created(){
            this.$http.get("https://wd2272200553mhzshj.wilddogio.com/posts/" + this.id+".json")
            .then(function(data){
                // this.blog = data.body;
                return data.json();
            }).then(function(data1){
                this.blog =  data1;
            })
        },
        methods:{
            Delete(){
                this.$http.delete("https://wd2272200553mhzshj.wilddogio.com/posts/" + this.id+".json")
                .then(response =>{
                    this.$router.push({path:"/"});
                })
            }
        }
    }
</script>
<style socped>
    .singleBlog{
        width: 800px;
        margin: 20px auto;
        padding: 50px;
        background-color: #eee;
    }
     .singleBlog article{
         width: 800px;
         margin: 0px auto;
         background-color: #fff;
         min-height: 150px;
         padding: 10px;
         text-indent:20px;
         font-size: 20px;
         line-height: 2em;
     }
     .btn123{
         width: 100%;
         text-align: center;
     }
     .btn123 button{
         width: 100px;
         height: 40px;
         line-height: 40px;
         text-align: center;
         background-color: #25C8FD;
         color: #fff;
         font-size: 18px;
         border: none;
     }
     .btn123 a{
         display: inline-block;
         width: 100px;
         height: 40px;
         line-height: 40px;
         text-align: center;
         background-color: #25C8FD;
         color: #fff;
         font-size: 18px;
         margin-left: 50px;
     }
</style>