<template>
    <div  class="showBlog">
        <h1>博客总览</h1>
        <input type="text" placeholder="搜索" class="search" v-model="search">
        <div v-for="blog in Serch" class="slingBlog" :key="blog">
           <router-link v-bind:to="'/blog/'+blog.id">
               <h1 v-raindow>{{blog.title | to-uppercase}}</h1>
            </router-link> 
            <article>
                {{blog.content | to-hidden }}
            </article>
        </div>
    </div>
</template>
<script>
    export default{
        name:"showBlog",
        data(){
            return{
                Blogs:[],
                search:"",
            }
        },
        created(){
            this.$http.get("https://wd2272200553mhzshj.wilddogio.com/posts.json")
            .then(function(data){
                // console.log(data.json())
                return data.json();

                // this.Blogs = data.body;
                // console.log(this.Blogs);
            })
            .then(function(data1){
                var blogsArray = [];
                for(let key1 in data1){
                    // console.log(key);
                    data1[key1].id = key1;
                    blogsArray.push(data1[key1]);
                }
                this.Blogs = blogsArray;
                // console.log(this.Blogs );
            })
        },
        computed:{
            Serch:function(){
                return this.Blogs.filter((blog) => {
                    return blog.title.match(this.search);

                })
            }
        },
        /*局部过滤器
        filters:{
            "to-uppercase":function(value){
                return value.toUpperCase();
            }
        }*/

    }
</script>
<style socped>
    .showBlog{
        width:800px;
        margin: 0px auto;
        background: #ccc;
        padding: 20px 50px;
    }
    h1{
        font-size: 25px;
        font-weight: normal;
    }
    .slingBlog{
        width:700px;
        padding: 20px 15px 20px 15px;
        background-color: #eee;
        margin: 30px auto 30px;
        text-indent:2em;
    }
    .search{
        display:block;
        width: 730px;
        height: 40px;
        line-height: 40px;
        margin: 0px auto;
        text-indent: 10px;
    }
    a{
        text-decoration: none;
    }
</style>