<template>
    <div class="editBlog">
        <h2>编辑博客</h2>
        <form action="" v-if="!submmitsd">
            <div class="Title">
                <label for="">博客标题</label>
                <input type="text" v-model="blog.title">  
            </div>
            <div class="content">
                <label for="">博客内容</label>
                <textarea v-model="blog.content" ></textarea>
            </div>
            <div class="checkbox" >
                <div>
                   <label for="">心情</label>
                    <input type="checkbox" value="心情" v-model="blog.classifies"> 
                </div>
                <div>
                    <label for="">随笔</label>
                    <input type="checkbox" value="随笔" v-model="blog.classifies">
                </div>
                <div>
                    <label for="">技术</label>
                    <input type="checkbox" value="技术" v-model="blog.classifies">
                </div>
                <div>
                    <label for="">分享</label>
                    <input type="checkbox" value="分享" v-model="blog.classifies">
                </div>
                
            </div>
            <div class="authors">
                <label for="">作者</label>
                <select v-model="blog.author">
                    <option v-for="author in authors" :key="author">
                        {{author}}
                    </option>
                </select>
            </div>
            <div class="btn">
                <button v-on:click.prevent="post">编辑博客</button>
            </div>
        </form>

        <hr>
        <div v-if="submmitsd">
            <h1>您的博客发布成功</h1>  
        </div>
        
    </div>
</template>
<script>
   export default{
       name:"editBlog",
       data(){
       return{
        id:this.$route.params.id,
        blog:{},
        authors:["herry","Daisy","Mark","Marry"],
        submmitsd:false,
       }
     },
     methods:{
         feathDate(){
             this.$http.get("https://wd2272200553mhzshj.wilddogio.com/posts/" + this.id+".json")
             .then(response =>{
                 this.blog = response.body;
             })
         },
         post:function(){
             this.$http.put("https://wd2272200553mhzshj.wilddogio.com/posts/" + this.id+".json",this.blog)
                .then(function( data ){
                    // console.log( data1 );
                    this.submmitsd = true;
                });
         }
     },
     created(){
         this.feathDate();
     }
   }
</script>
<style scoped>
    .addBlog{
        width: 800px;
        height: auto;
        background-color: #eee;
        margin: 0px auto;
        padding: 50px;
    }
    h2{
        width: 700px;
        margin: 20px auto;
    }
    .Title{
        width: 730px;
        height: auto;
        margin: 0px auto;
    }
    .Title label,.content label,.authors label{
        display: block;
        font-size:16px;
        text-align: left;
        line-height: 2em;
    }
    .Title input{
        width: 100%;
        height: 35px;
        line-height: 35px;
        border: 1px solid #666;
        text-indent: 10px;
    }
    .content{
        width:730px;
        height: auto;
        margin: 20px auto;
    }
    .content textarea{
        width: 100%;
        height: 200px;
        resize: unset;
        overflow: auto;
        font-size: 16px;
        padding: 10px;
    }
    .checkbox{
        width:730px;
        display: flex;
        margin: 0px auto;
    }
    .checkbox div{
        flex: 1;
    }
    .authors{
        width: 730px;
        height: auto;
        margin: 20px auto;
    }
    .authors>select{
        width: 100%;
        height: 35px;
    }
    .btn{
        width: 730px;
        height: 35px;
        line-height: 35px;
        text-align: center;
        margin: 0px auto;
    }
    .btn button{
        width: 100%;
        color: #fff;
        background-color: red;
        height: 100%;
        font-size: 16px;
        border: none;
    }
    .blog_show{
        width: 700px;
        margin: 20px auto;
    }
</style>